.Header {
    height: 56px;
    background-color: #fff;
    border-bottom: 1px solid #e6e8ea;
    box-shadow: 0px 0px 4px 0 rgba(0, 0, 0, 0.2);
    position: fixed;
    left: 0;
    right: 0;
    z-index: 1;
}

.Header__container {
    display: flex;
    align-items: center;
    position: relative;
    justify-content: space-between;
}

