.searchHeader {
    display: flex;
    align-items: center;
    height: 40px;
    width: 304px;
}

.searchHeader-searchContainer__backIcon {
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 8px;
    border-radius: 50%;
    opacity: 0.7;
    cursor: pointer;
    &:hover {
        background-color: #F2F2F2;
    }
    display: none;
}

.searchHeader__link {
    height: 40px;
    width: 40px;
    margin: 0 8px 0 16px;
}

.searchHeader__img {
    height: 40px;
    width: 40px;
    border-radius: 50%;
}

.searchHeader-searchContainer {
    background-color: #F0F2F5;
    display: flex;
    align-items: center;
    width: 100%;
    height: 40px;
    border-radius: 120px;
    align-items: center;
}

.searchHeader-searchContainer__icon {
    margin-left: 12px;
    margin-top: 6px;
    opacity: 0.5;
    font-size: 16px;
}

.searchHeader-searchContainer__input {
    margin-left: 12px;
    border-style: none;
    background-color: #F0F2F5;
    font-size: 15px;
    border-top-right-radius: 120px;
    border-bottom-right-radius: 120px;
    width: 200px;
    outline: none;
}
