.homepage-newfeed__post {
    background-color: #fff;
    margin: 20px 4px;
    border-radius: 10px;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.15);
}

.homepage-newfeed__create-post-wrapper {
    display: flex;
    margin: 0 16px;
    padding: 16px 0 12px;
    position: relative;

    &::after {
        content: '';
        position: absolute;
        height: 1px;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: #e5e7eb;
    }
}

.homepage-newfeed__create-post-img {
    width: 40px;
    height: 40px;
    border-radius: 50%;

    &:hover {
        cursor: pointer;
        filter: brightness(95%);
    }
}

.homepage-newfeed__create-post {
    margin-left: 10px;
    border-radius: 100px;
    background-color: #F0F2F5;
    width: 100%;
    display: flex;
    align-items: center;

    &:hover {
        cursor: pointer;
        filter: brightness(95%);
    }
}

.homepage-newfeed__create-post-span {
    margin-left: 10px;
    color: #65676B;
    font-size: 17px;
}

.homepage-newfeed__post-function {
    margin: 8px 16px 0;
    padding-bottom: 12px;
}

.homepage-newfeed__post-function-list {
    list-style: none;
    display: flex;
    justify-content: space-between;
}

.homepage-newfeed__post-function-item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 186px;
    height: 40px;
    padding: 8px;
    border-radius: 8px;
    
    &:hover {
        cursor: pointer;
        background-color: #f2f2f2;
    }
}

.homepage-newfeed__post-function-img {
    font-size: 24px;
}

.homepage-newfeed__post-function-img--red {
    fill: #f3425f;
}

.homepage-newfeed__post-function-img--green {
    fill: #45bd62;
}

.homepage-newfeed__post-function-img--yellow {
    fill: #f7b928;
}

.homepage-newfeed__post-function-span {
    font-size: 15px;
    color: #65676B;
    font-weight: 500;
    margin-left: 8px;
}
